import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import HelloLouise from '@/components/HelloLouise'
import CodeCamp from '@/components/CodeCamp'
import AboutMe from '@/components/AboutMe'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/sample',
      name: 'HelloLouise',
      component: HelloLouise
    },
    {
      path: '/codecamp',
      name: 'CodeCamp',
      component: HelloLouise
    },
     {
      path: '/about',
      name: 'AboutMe',
      component: AboutMe
    }
  ]
})



