<template>
  <div class="hello">
      <link rel="stylesheet" type="text/css" href="https://stackpath.bootstrapcdn.com/bootstrap/4.1.2/css/bootstrap.min.css">
      <link rel="stylesheet" type="text/css" href="https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css">
      <link rel="stylesheet" type="text/css" href="https://cdn.datatables.net/1.10.19/css/jquery.dataTables.min.css">
    <h1>{{ msg }}</h1>
     <div class="row">
        <div class="col-md-3">
          
        </div>
         <div class="col-md-6">
             <ul>
      <li>
      <a href="http://localhost:8080/#/" class="btn btn-warning text-info" >
           <i class="fa fa-reply"></i> Go back to Home
        </a>
     
      </li>
      
    </ul>
                <label style="color: red;">{{ error }}</label>
                <label class="text-success">{{ success }}</label>
                 <table class="table table-bordered " style="font-size: 16px;" id="todo">
                 <thead>
                    <tr>

                    <th colspan="3">TASKS</th>
                    <th></th>
                  
          
                  </tr>
                 </thead>
                 <tbody>
                    <tr v-for="(item,index) in items">
                      <td colspan="2"><span v-if="!item.edit"> {{ item.text }}</span>
                          <input  v-else v-model="item.text" @keydown.enter="editTodo(item)" class="form-control">
                        </td>
                       <td colspan="2">
                        <button class="btn btn-xs btn-danger btn-circle" @click="deleteTodo(index)"><i class="fa fa-trash"></i> remove</button>
                        &nbsp;
                        <button class="btn btn-xs btn-info btn-circle" @click="editTodo(item)">
                          <span v-if="!item.edit"> <i class="fa fa-edit"></i> edit</span>
                           <span v-else> <i class="fa fa-save"></i> save</span>

                       </button></td>
                  </tr>
                 </tbody>
                 
                 
                </table>
              
               
                   
              <input v-model="message" @keydown.enter="doThis" class="form-control">
                <br>
             
                   <button @click="addItem" class="btn btn-info pull-right"><i class="fa fa-plus"></i>Add</button>
             

             
        </div>

         <div class="col-md-3">
          
        </div>
       
     </div>

     
        
  </div>
</template>
<script type="text/javascript" src="https://stackpath.bootstrapcdn.com/bootstrap/4.1.2/js/bootstrap.min.js"></script>
<script type="text/javascript" src="https://cdn.datatables.net/1.10.19/js/jquery.dataTables.min.js"></script>
<script type="text/javascript">
  $(function(){
    $('#todo').DaTaTables();

  })
</script>
<script>
export default {
  name: 'HelloLouise',
  data () {
    return {
      msg: ' TO DO LIST',
      message: '',
      error: '',
      success: '',
      items : [
         
      ]
    }
  },
  filters: {
         capitalize : function(value){
          if(!value) return "";
          value =value.toString()
          return value.charAt(0).toUpperCase() + value.slice(1)
         }
      },
   methods:  {
       doThis: function message() {
            if(this.message.length !== 0) {
                 this.items.push({text:this.message,edit :false})
                  this.error = ''
                  this.success = 'New task has been added !'
                  this.message = ''
            }
            else {
              this.error = 'Input is required'
              this.success = ''
            }
       },
      addItem: function() {
            if(this.message.length !== 0) {
                 this.items.push({text:this.message,edit :false})
                  this.error = ''
                  this.success = 'New task has been added !'
                  this.message = ''
            }
            else {
              this.error = 'Input is required !'
              this.success = ''
            }
           
      },
      deleteTodo: function(index) {

     
          var r = confirm("Are you sure you want to delete this task ?");
          if (r == true) {
      
              this.items.splice(index,1);
              this.success = 'Task has been deleted !'
              this.error = ''
          } 
          else {
              this.success = ''
              this.error = ''
          }

         

      },
      editTodo: function(item) {

           item.edit = !item.edit
           if (!item.edit) {
             this.success = 'Task has been updated !'
             this.error = ''
         }
         else {
             this.success = ''
             this.error = ''
         }

      }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}


a {
  color: #42b983;
}
</style>



